import React, { useState, useEffect, useRef, useCallback} from 'react';
import './Gallery.css';
import parsedData from './data';
import moonBirdsData from './moonbirdsdata';
import TwitterIcon from "./HPImages/twitter.svg";
import DiscordIcon from "./HPImages/discord.svg";
import OMIcon from "./HPImages/OMLogo.png";
import { Link } from 'react-router-dom';
import 'intersection-observer';

function useLazyLoading(cellSelector, options, realData) {
  useEffect(() => {
    const observer = new IntersectionObserver((entries, observerInstance) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          const lazyCell = entry.target;
          const img = lazyCell.querySelector('img');
          const src = img.dataset.src;
          img.src = src;
          lazyCell.classList.add('loaded'); // Change this line
          observerInstance.unobserve(lazyCell);
        }
      });
    }, options);

    const cells = document.querySelectorAll(cellSelector);

    cells.forEach(cell => observer.observe(cell));

    return () => {
      observer.disconnect();
    };
  }, [cellSelector, options, realData]);
}

function getSortedLabels(labels, option, rarityMode, activeData) {
  let sortedLabels = [...labels];

  // Sort labels alphabetically
  sortedLabels.sort();

  if (rarityMode) {
    // Calculate label counts
    const labelCounts = sortedLabels.reduce((acc, label) => {
      acc[label] = activeData.filter(row => row[option] === label).length;
      return acc;
    }, {});

    // Sort labels by their count when rarity mode is on
    sortedLabels.sort((a, b) => {
      return labelCounts[a] - labelCounts[b];
    });
  }

  return sortedLabels;
}

function Gallery() {
   // Only show the first 100 grids by default
  const [realData, setrealData] = useState(parsedData.slice(0, 100));
  const [activeData, setActiveData] = useState(parsedData.slice(0, 100));
  const gridContainerRef = useRef(null);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 100; // Adjust this value as needed
  const [rarityMode, setRarityMode] = useState(false);
  const [searchInput, setSearchInput] = useState('');

  useLazyLoading('.grid-cell', { rootMargin: '100px' }, activeData);

  const handlePunksClick = () => {
    setActiveData(parsedData);
    setrealData(parsedData);
    setCurrentPage(1);
    setSelectedFilters({
      // Reset the selected filters for Punks
      Background: new Set(),
      Body: new Set(),
      Feathers: new Set(),
      Eyes: new Set(),
      Mouth: new Set(),
      Beak: new Set(),
      Eyewear: new Set(),
      Glasses: new Set(),
      Headwear: new Set(),
      Hats: new Set(),
      Outerwear: new Set(),
    });
  };  
  
  const handleMoonbirdsClick = () => {
    setActiveData(moonBirdsData);
    setrealData(moonBirdsData);
    setSelectedFilters({
      Background: new Set(),
      Body: new Set(),
      Feathers: new Set(),
      Eyes: new Set(),
      Mouth: new Set(),
      Beak: new Set(),
      Eyewear: new Set(),
      Glasses: new Set(),
      Headwear: new Set(),
      Hats: new Set(),
      Outerwear: new Set(),
    });
    setFilterOptionExpanded({
      Background: false,
      Body: false,
      Feathers: false,
      Eyes: false,
      Mouth: false,
      Beak: false,
      Eyewear: false,
      Glasses: false,
      Headwear: false,
      Hats: false,
      Outerwear: false,
    });
    setCurrentPage(1);
    setSelectedFilters(moonBirdsFilterOptions);
    
  
    // Append a timestamp to the image URLs to force the browser to fetch the updated images
    const images = document.querySelectorAll('img');
    images.forEach((img) => {
      const src = img.dataset.src;
      img.dataset.src = `${src}?t=${Date.now()}`;
    });

  const filteredData = moonBirdsData.filter(row => {
    return Object.keys(selectedFilters).every(option => {
      return selectedFilters[option].size === 0 || selectedFilters[option].has(row[option]);
    });
  });
  setActiveData(filteredData);
};

  const handleTwitterClick = () => {
    window.open("https://twitter.com/LitecoinPunks", "_blank");
  };

  const handleDiscordClick = () => {
    window.open("https://discord.gg/litecoinpunks", "_blank");
  };

  const handleOMClick = () => {
    window.open("https://ordinals.market/collection/ordinals/litecoin-punks", "_blank");
  };

   // Get all the possible filter options from the data
   const filterOptions = {
    Background: new Set(),
    Body: new Set(),
    Feathers: new Set(),
    Eyes: new Set(),
    Mouth: new Set(),
    Beak: new Set(),
    Eyewear: new Set(),
    Glasses: new Set(),
    Headwear: new Set(),
    Hats: new Set(),
    Outerwear: new Set(),
  };

  const moonBirdsFilterOptions = {
    Background: new Set(),
    Body: new Set(),
    Feathers: new Set(),
    Eyes: new Set(),
    Mouth: new Set(),
    Beak: new Set(),
    Eyewear: new Set(),
    Glasses: new Set(),
    Headwear: new Set(),
    Hats: new Set(),
    Outerwear: new Set(),
  };  
  
  activeData.forEach(row => {
    Object.keys(filterOptions).forEach(option => {
      if (row[option]) { // Only add the option to the set if it exists in the data
        filterOptions[option].add(row[option]);
      }
    });
  });  
  

  // State to keep track of which filter options are currently selected
  const [selectedFilters, setSelectedFilters] = useState({
    Background: new Set(),
    Body: new Set(),
    Feathers: new Set(),
    Eyes: new Set(),
    Mouth: new Set(),
    Beak: new Set(),
    Eyewear: new Set(),
    Glasses: new Set(),
    Headwear: new Set(),
    Hats: new Set(),
    Outerwear: new Set(),
  });

  // State to keep track of whether each filter option is currently expanded or collapsed
  const [filterOptionExpanded, setFilterOptionExpanded] = useState({
    Background: false,
    Body: false,
    Feathers: false,
    Eyes: false,
    Mouth: false,
    Beak: false,
    Eyewear: false,
    Glasses: false,
    Headwear: false,
    Hats: false,
    Outerwear: false,
  });

  const toggleFilterOption = (option, value) => {
    const newSelectedFilters = { ...selectedFilters };
  
    // If the current filter is the only one selected, deselect it and show all options
    if (newSelectedFilters[option].size === 1 && newSelectedFilters[option].has(value)) {
      newSelectedFilters[option].delete(value);
    } else {
      if (newSelectedFilters[option].has(value)) {
        newSelectedFilters[option].delete(value);
      } else {
        newSelectedFilters[option].add(value);
      }
    }
  
    setSelectedFilters(newSelectedFilters);
  
    const newData = realData.filter(row => {
      return Object.keys(newSelectedFilters).every(option => {
        return newSelectedFilters[option].size === 0 || newSelectedFilters[option].has(row[option]);
      });
    });
  
    setActiveData(newData);
  };
  
  const [filterLabelCounts, setFilterLabelCounts] = useState({
    Background: {},
    Body: {},
    Feathers: {},
    Eyes: {},
    Mouth: {},
    Beak: {},
    Eyewear: {},
    Glasses: {},
    Headwear: {},
    Hats: {},
    Outerwear: {},
  });

  useEffect(() => {
    const newFilterLabelCounts = {
      Background: {},
      Body: {},
      Feathers: {},
      Eyes: {},
      Mouth: {},
      Beak: {},
      Eyewear: {},
      Glasses: {},
      Headwear: {},
      Hats: {},
      Outerwear: {},
    };
  
    realData.forEach(row => {
      Object.keys(filterOptions).forEach(option => {
        if (row[option]) {
          if (newFilterLabelCounts[option][row[option]]) {
            newFilterLabelCounts[option][row[option]]++;
          } else {
            newFilterLabelCounts[option][row[option]] = 1;
          }
        }
      });
    });
  
    setFilterLabelCounts(newFilterLabelCounts);
  }, [realData]);  

 // Sort function to use when sorting the filtered data
 const [sortOrder, setSortOrder] = useState("asset");

 const handleSortOrderChange = () => {
  setRarityMode(!rarityMode);
   if (sortOrder === "asset") {
     setActiveData(activeData.slice().sort((a, b) => a.Rarity - b.Rarity || a['Asset #'] - b['Asset #']));
     setSortOrder("rarity");
   } else {
     setActiveData(activeData.slice().sort((a, b) => a['Asset #'] - b['Asset #']));
     setSortOrder("asset");
   }
 };

 const handleSearchInputChange = (e) => {
    // Ensure that the search input only accepts numbers
    const value = e.target.value.replace(/\D/g, '');
    setSearchInput(value);

    // Reset the currentPage state when the search input is empty
    if (value === '') {
      setCurrentPage(1);
    }
  };


  useEffect(() => {
    if (realData && realData.length > 0) {
      const filteredData = realData.filter(row => {
        const matchesSearch = searchInput === '' || row['Asset #'].toString() === searchInput;
        return matchesSearch && Object.keys(selectedFilters).every(option => {
          return selectedFilters[option].size === 0 || selectedFilters[option].has(row[option]);
        });
      });
      setActiveData(filteredData);
    }
  }, [realData, selectedFilters, searchInput]);  
  
  const loadMoreItems = useCallback(() => {
    setrealData((prevData) => [
      ...prevData,
      ...activeData.slice(prevData.length, prevData.length + itemsPerPage),
    ]);
  }, [activeData, itemsPerPage]);

  useEffect(() => {
    const handleScroll = (e) => {
      const gridContainer = gridContainerRef.current;
      const isBottom = gridContainer.scrollHeight - gridContainer.scrollTop === gridContainer.clientHeight;
  
      if (isBottom) {
        setCurrentPage((prevPage) => prevPage + 1);
        loadMoreItems();
      }
    };
  
    const gridContainer = gridContainerRef.current;
    gridContainer.addEventListener("scroll", handleScroll);
  
    return () => {
      gridContainer.removeEventListener("scroll", handleScroll);
    };
  }, [activeData, loadMoreItems]);  

  const loadMoreObserver = useCallback((node) => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setCurrentPage((prevPage) => prevPage + 1);
          loadMoreItems();
          observer.disconnect(); // Disconnect the observer after loading more items
        }
      });
    });
  
    if (node) {
      observer.observe(node);
    }
  
    return () => {
      if (node) {
        observer.unobserve(node);
      }
    };
  }, [loadMoreItems]);  

  return (
    <div className="gallery">
      <header className="header">
        <Link to="/" className="button">
          LLabs
        </Link>
        <div className="button-container">
          <Link to="/gallery" className="button-right">
            Gallery
          </Link>
          <Link to="/arcade" className="button-right">
            Arcade
          </Link>
          <Link to="/Calendar" className="button-right">
            Calendar
          </Link>
          <button className="button-alt" onClick={handleTwitterClick}>
            <img src={TwitterIcon} alt="Twitter icon" />
          </button>
          <button className="button-alt" onClick={handleDiscordClick}>
            <img src={DiscordIcon} alt="Discord icon" />
          </button>
          <button className="button-om" onClick={handleOMClick}>
            <img src={OMIcon} alt="OM icon" />
          </button>
        </div>
      </header>
      <main className="gallery-main">
        <div className="gallery-container">
          <div className="flex">
            <div className="left-column">
              <div className="filter-header">
                <h1 className="filter-text">
                  <span>Filter</span>
                </h1>
              </div>
              <div className="filter-menu">
                <div className="rarity-switch">
                  <span className="rarity-switch-text">Rarity Mode</span>
                  <button className="grid-header-button" onClick={handleSortOrderChange}>
                    <span>{sortOrder === "asset" ? "Off" : "On"}</span>
                  </button>
                </div>
                <div className="search-by-number">
                  <label htmlFor="search-number"></label>
                  <input
                    type="number"
                    id="search-number"
                    value={searchInput}
                    onChange={handleSearchInputChange}
                    placeholder="Sort by serial..."
                    className="search-number-box"
                  />
                </div>
                {Object.keys(filterOptions).map(option => {
                  const labels = getSortedLabels(filterOptions[option], option, rarityMode, activeData);
                  const hasLabels = labels.length > 0;
                  return (
                    hasLabels && (
                      <div className="filter-menu-item" key={option}>
                        <div className="filter-menu-label" onClick={() => setFilterOptionExpanded(prevState => ({
                          ...prevState,
                          [option]: !prevState[option],
                        }))}>
                          <label htmlFor={`${option}-select`}>{option}</label>
                          <div className="filter-checkbox-toggle">{filterOptionExpanded[option] ? "-" : "+"}</div>
                        </div>
                          <div className={`filter-checkboxes-list label-menu ${filterOptionExpanded[option] ? "label-menu-open" : ""}`}>
                            {labels.map(value => (
                              <label key={value}>
                                <input
                                  type="checkbox"
                                  value={value}
                                  checked={selectedFilters[option].has(value)}
                                  onChange={() => toggleFilterOption(option, value)}
                                />
                                {value} ({filterLabelCounts[option][value] || 0})
                              </label>
                            ))}
                          </div>
                      </div>
                    )
                  );
                })}
              </div>
              {/* Content for the left column goes here */}
            </div>
            <div className="right-column">
            <div className="grid-header">
              <div className="grid-header-button-container">
                <button className="grid-header-button" onClick={handlePunksClick}>
                  <span>Punks</span>
                </button>
                <button className="grid-header-button" onClick={handleMoonbirdsClick}>
                  <span>Moonbirds</span>
              </button>
              </div>
            </div>
            <div className="grid-container" ref={gridContainerRef}>
              {activeData
                .sort(
                  sortOrder === "asset"
                    ? (a, b) => a["Asset #"] - b["Asset #"]
                    : (a, b) => b.Rarity - a.Rarity
                )
                .slice(0, currentPage * itemsPerPage)
                .map((row, index, self) => {
                  const isLastGridCell = index === self.length - 1;
                  return (
                    <div
                      className="grid-cell"
                      key={row["Asset #"]}
                      loading="lazy"
                      ref={isLastGridCell ? loadMoreObserver : null}
                    >
                      <img
                        data-src={row["Image"]}
                        alt={`${row["Item #"]}`}
                        key={`${realData}-${row["Item #"]}`}
                        loading="lazy"
                        className="lazy-image"
                      />
                      <div className="punk-number">{`${row["Item #"]}`}</div>
                      <div className="inscription-number">{`INSC. ${row["Inscription #"]}`}</div>
                      <div className="punk-rarity">{`RANK ${row["Rank Value"]}`}</div>
                    </div>
                  );
                })}
            </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

export default Gallery;