import React, { useState, useEffect } from "react";
import Papa from "papaparse";
import "./Arcade.css";
import TwitterIcon from "./HPImages/twitter.svg";
import DiscordIcon from "./HPImages/discord.svg";
import OMIcon from "./HPImages/OMLogo.png";
import { Link } from "react-router-dom";
import gamesCSV from "./games.csv";

function Arcade() {
  const handleTwitterClick = () => {
    window.open("https://twitter.com/LitecoinPunks", "_blank");
  };

  const handleDiscordClick = () => {
    window.open("https://discord.gg/litecoinpunks", "_blank");
  };

  const handleOMClick = () => {
    window.open(
      "https://ordinals.market/collection/ordinals/litecoin-punks",
      "_blank"
    );
  };

  const [games, setGames] = useState([]);

  useEffect(() => {
    Papa.parse(gamesCSV, {
      download: true,
      header: true,
      complete: (results) => {
        setGames(results.data);
      },
    });
  }, []);

  const renderGames = () => {
    return games.map((game, index) => (
      <div key={index} className="canvas-container">
        <span className="game-name">{game.title}</span>
        <div className="game-frame">
          <img
            src={game.preview}
            alt={`${game.title} preview`}
            className="game-preview"
            onClick={() => window.open(game.src, "_blank")}
          />
        </div>
        <span
          className="insc-number"
          onClick={() => window.open(game.inscription_url, "_blank")}
        >
          {game.inscription_number}
        </span>
      </div>
    ));
  };  

  return (
    <div className="homepage">
      <header className="header">
        <Link to="/" className="button">
          LLabs
        </Link>
        <div className="button-container">
          <Link to="/gallery" className="button-right">
            Gallery
          </Link>
          <Link to="/arcade" className="button-right">
            Arcade
          </Link>
          <Link to="/Calendar" className="button-right">
            Calendar
          </Link>
          <Link to="/moonbirds" className="button-right">
            Moonbirds
          </Link>
          <button className="button-alt" onClick={handleTwitterClick}>
            <img src={TwitterIcon} alt="Twitter icon" />
          </button>
          <button className="button-alt" onClick={handleDiscordClick}>
            <img src={DiscordIcon} alt="Discord icon" />
          </button>
          <button className="button-om" onClick={handleOMClick}>
            <img src={OMIcon} alt="OM icon" />
          </button>
        </div>
      </header>
      <main className="main">
      <div className="all-games">{renderGames()}</div>
      </main>
    </div>
  );
}

export default Arcade;
