import React, { useState, useEffect } from "react";
import "./Calendar.css";
import TwitterIcon from "./HPImages/twitter.svg";
import DiscordIcon from "./HPImages/discord.svg";
import OMIcon from "./HPImages/OMLogo.png";
import { Link } from "react-router-dom";
import Papa from "papaparse";
import dropsCSV from "./drops.csv";
import featuredCSV from "./featured.csv";

function Calendar() {
  const [dropData, setDropData] = useState([]);
  const [featuredData, setFeaturedData] = useState(null);

  useEffect(() => {
    Papa.parse(dropsCSV, {
      header: true,
      download: true,
      skipEmptyLines: true,
      complete: function (results) {
        setDropData(results.data);
      },
    });
  }, []);

  useEffect(() => {
    Papa.parse(featuredCSV, {
      header: true,
      download: true,
      skipEmptyLines: true,
      complete: function (results) {
        setFeaturedData(results.data[0]);
      },
    });
  }, []);

  const handleTwitterClick = () => {
    window.open("https://twitter.com/LitecoinPunks", "_blank");
  };

  const handleDiscordClick = () => {
    window.open("https://discord.gg/litecoinpunks", "_blank");
  };

  const handleOMClick = () => {
    window.open(
      "https://ordinals.market/collection/ordinals/litecoin-punks",
      "_blank"
    );
  };

  return (
    <div className="homepage">
      <header className="header">
        <Link to="/" className="button">
          LLabs
        </Link>
        <div className="button-container">
          <Link to="/gallery" className="button-right">
            Gallery
          </Link>
          <Link to="/arcade" className="button-right">
            Arcade
          </Link>
          <Link to="/Calendar" className="button-right">
            Calendar
          </Link>
          <button className="button-alt" onClick={handleTwitterClick}>
            <img src={TwitterIcon} alt="Twitter icon" />
          </button>
          <button className="button-alt" onClick={handleDiscordClick}>
            <img src={DiscordIcon} alt="Discord icon" />
          </button>
          <button className="button-om" onClick={handleOMClick}>
            <img src={OMIcon} alt="OM icon" />
          </button>
        </div>
      </header>
      <main className="calendar-main">
        <div className="calendar-header">
          <h1 className="calendar-title">ORD Calendar</h1> {/* Removed "button" class */}
          <div className="featured-drop">Featured</div>
        </div>
        {
          featuredData && (
            <div className="featured-grid-cell">
            <h3>{featuredData.name}</h3>
              <img src={featuredData.image} alt="Featured Drop" />
              <p className="insc">{featuredData.insc}</p>
              <p className="date"> {featuredData.date}</p>
              <div className="social-icons">
                <button
                  className="button-alt"
                  onClick={() => window.open(featuredData.twitter, "_blank")}
                >
                  <img src={TwitterIcon} alt="Twitter" />
                </button>
                <button
                  className="button-alt"
                  onClick={() => window.open(featuredData.discord, "_blank")}
                >
                  <img src={DiscordIcon} alt="Discord" />
                </button>
              </div>
            </div>
          )
        }
        <div className="calendar-grid-container">
          {dropData.map((drop, index) => (
            <div key={index} className="calendar-grid-cell">
              <h3>{drop.name}</h3> {/* Add this line to display the name */}
              <img src={drop.image} alt="Drop" />
              <p className="insc">{drop.insc}</p> {/* Add this line to display the insc */}
              <p className="date">{drop.date}</p>
              <div className="social-icons">
                <button className="button-alt" onClick={() => window.open(drop.twitter, "_blank")}>
                  <img src={TwitterIcon} alt="Twitter" />
                </button>
                <button className="button-alt" onClick={() => window.open(drop.discord, "_blank")}>
                  <img src={DiscordIcon} alt="Discord" />
                </button>
              </div>
            </div>
          ))}
        </div>
        <div className="submit-container">
          <span className="submit-text">Want your collection to be listed? Submit</span>
          <a
            className="submit-button"
            href="https://forms.gle/ih1F3N1Cg2RnsvaY9"
            target="_blank"
            rel="noopener noreferrer"
          >
            Here
          </a>
        </div>
      </main>
    </div>
  );
}

export default Calendar;
